import React, { useEffect, useRef, useState } from "react";
import { dbService, storageService } from "fbase";
import {
  addDoc,
  collection,
  doc,
  getDocs,
  onSnapshot,
  orderBy,
  query,
} from "firebase/firestore";

import Tweet from "components/Tweet";
import { ref, uploadString, getDownloadURL } from "firebase/storage";
import { v4 as uuidv4 } from "uuid";
import { clear } from "@testing-library/user-event/dist/clear";
function Home({ userObject }) {
  const [tweet, setTweet] = useState("");
  const [tweets, setTweets] = useState([]);
  const [photoLink, setPhotoLink] = useState(); //     

  const onFileChange = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();
    reader.onloadend = (finishedEvent) =>
      setPhotoLink(finishedEvent.currentTarget.result);
    reader.readAsDataURL(file);
  };

  useEffect(() => {
    // getTweets();
    const q = query(
      collection(dbService, "tweets"),
      orderBy("createdAt", "desc")
    );
    onSnapshot(q, (snapshot) => {
      //      .(  )
      const tweetArr = snapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));
      console.log(tweetArr);
      setTweets(tweetArr);
    });
  }, []);
  console.log(tweets);

  const onSubmit = async (e) => {
    e.preventDefault();
    // create document
    setTweet("");

    //    

    let downloadURL = "";
    if (photoFile.current.value) {
      clearPhoto(); //   
      const fileRef = ref(storageService, `${userObject.uid}/${uuidv4()}`);
      const response = await uploadString(fileRef, photoLink, "data_url");
      downloadURL = await getDownloadURL(response.ref);
      console.log(downloadURL);
    }

    try {
      const docRef = await addDoc(collection(dbService, "tweets"), {
        text: tweet,
        createdAt: Date.now(),
        creatorId: userObject.uid,
        downloadURL,
      });
      console.log("tweeted with ID: ", docRef.id);
    } catch (e) {
      console.error("Error adding document: ", e);
    }
  };

  // for Photo Attachment

  const photoFile = useRef();
  const clearPhoto = () => {
    setPhotoLink(null);
    photoFile.current.value = null;
  };

  //  setTweets();
  return (
    <div>
      <form>
        <input
          type="text"
          placeholder="  "
          maxLength={120}
          value={tweet}
          onChange={(e) => setTweet(e.target.value)}
        />
        <input
          type="file"
          accept="image/*"
          onChange={onFileChange}
          ref={photoFile}
        />
        <input type="submit" value="" onClick={onSubmit} />
        {photoLink && (
          <div>
            <img width="50px" height="50px" src={photoLink}></img>
            <button onClick={clearPhoto}> </button>
          </div>
        )}
      </form>

      {/* <button onClick={loadTweets}>load Tweets</button> */}
      {tweets.map((tweet) => (
        <Tweet
          key={tweet.id}
          tweetObj={tweet}
          isOwner={tweet.creatorId === userObject.uid}
        />
      ))}
    </div>
  );
}
export default Home;
